import React, { Component } from 'react';
import { MDBDataTable, MDBBtn } from "mdbreact";
import './cards.css';

class cards extends Component {
  constructor(props) {
          super(props);
}
  state = {
    columns: [
      {
        field: 'name',
        label: 'Name',
        sort:'asc'
      },
      {
        field: 'fatherName',
        label: 'Father Name',
        sort:'asc'
      },
      {
        field: 'courseName',
        label: 'Course Name',
        sort:'asc'
      },
      {
        field: 'motherName',
        label: 'Mother Name',
        sort:'asc'
      },
      {
        field: 'address',
        label: 'Address',
        sort:'asc'
      },
      {
        field: 'phone',
        label: 'Phone',
        sort:'asc'
      },
      {
        field: 'edit',
        label: 'Edit',
        sort:'asc'
      },
      {
        field: 'delete',
        label: 'Delete'
      }
    ],
    rows: []
  }

  componentDidMount() {
    this.getStudent();
  }

  getStudent = () => {
    fetch("https://api.cedu.in/V2/api/getStudent", {
     method: 'post',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
    })
      .then(res => res.json())
      .then(json => {
        let rows = [];
        console.log(json);
        json.response.forEach(item => rows.push({
          id: item._id,
          name: item.name,
          fatherName: item.fatherName,
          phone: item.phone,
          courseName:item.courseName,
          motherName:item.motherName,
          address:item.address,
          delete: <MDBBtn onClick={() => this.deleteStudent(item._id)}><i className="fa fa-trash"></i></MDBBtn>,
          edit: <MDBBtn onClick={() => this.editStudent(item._id)}><i className="fa fa-edit"></i></MDBBtn>

        }));

        this.setState({ rows });
      })
      .catch(err => console.error(err));
  }
  deleteStudent = (id) =>{
      fetch("https://api.cedu.in/V2/api/deleteStudent", {
     method: 'post',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
       body: JSON.stringify({
        _id: id,
      })
    })
      .then(res => res.json())
      .then(json => {
        // console.log("vdsv",json)
        if (json.response_code == 200) this.getStudent();
      })
      .catch(err => console.error(err));
  }
  render() {
    return (
        <MDBDataTable
          striped
          bordered
          hover
          order = {["courseName","asc"]}
          data={{ columns: this.state.columns, rows: this.state.rows }}
        />
    );
  }
};

export default cards;